"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";

const tabs = [
  { href: "/main", label: "기부" },
  { href: "/main/ranking", label: "랭킹" },
  { href: "/main/badges", label: "배지" },
];

export default function Navbar() {
  const pathname = usePathname();

  return (
    <header
      className="
        fixed top-0 left-0 right-0 z-50
        bg-[#0a0f1f]/70 
        backdrop-blur-xl
        border-b border-white/10
      "
    >
      <nav className="max-w-5xl mx-auto flex items-center justify-between px-6 py-4">
        {/* 로고 */}
        <div className="text-lg font-bold text-indigo-300">GiveChain</div>

        {/* 탭 메뉴 */}
        <ul className="flex items-center gap-8 text-sm font-medium">
          {tabs.map((tab) => {
            const active = pathname === tab.href;
            return (
              <li key={tab.href}>
                <Link
                  href={tab.href}
                  className={
                    active
                      ? "text-indigo-300 border-b-2 border-indigo-400 pb-1"
                      : "text-gray-400 hover:text-white pb-1 transition-colors"
                  }
                >
                  {tab.label}
                </Link>
              </li>
            );
          })}
        </ul>

        {/* 로그인한 사용자 오른쪽 버튼 */}
        <div className="flex items-center gap-3">
          <span className="text-gray-300 text-sm">김동균님</span>
          <button className="text-sm bg-indigo-600 hover:bg-indigo-500 text-white px-4 py-1.5 rounded-lg transition">
            로그아웃
          </button>
        </div>
      </nav>
    </header>
  );
}